<template>
    <div>
        <h4>Âü∫Êú¨‰ø°ÊÅØ</h4>
        <el-input v-model="username" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"></el-input>
        <el-input v-model="password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>
        <el-input v-model="confirmPass" placeholder="ËØ∑ËæìÂÖ•Á°ÆËÆ§ÂØÜÁ†Å"></el-input>
        <h4>Êî∂Ë¥ßÂú∞ÂùÄ</h4>
        <el-input v-model="province" placeholder="ÁúÅ"></el-input>
        <el-input v-model="city" placeholder="Â∏Ç"></el-input>
        <el-input v-model="county" placeholder="Âéø"></el-input>
        <el-input v-model="detail" placeholder="ÂÖ∑‰ΩìÂú∞ÂùÄ"></el-input>
         <el-input v-model="tel" placeholder="ÁîµËØù"></el-input>

        <el-button type="primary" @click="register">Ê≥®ÂÜå</el-button>

    </div>
</template>
<script lang="ts">
import Vue from 'vue'
import axios from 'axios'
import Cookies from 'js-cookie'

const config = require('../config/conf.js');

export default Vue.extend({
   name: 'Register',
   created(){

   },
   methods:{
     register(){
       console.log(this.username);
       console.log(this.password);
      var that = this;
                axios({
                        method:'post',
                        url: config.url+ "register",
                        params:{
                            username: this.username,
                            password: this.password,
                            Province: this.province,
                            City: this.city,
                            County: this.county,
                            Detail: this.detail,
                            Tel: this.tel
                        }
                }).then(function(resp){
                        that.$nextTick(function () {
                          Cookies.set('token',resp.data.Token);
                          Cookies.set('username',this.username);
                          Cookies.set('Province',this.province);
                          Cookies.set('City',this.City);
                          Cookies.set('County',this.county);
                          Cookies.set('Detail',this.detail);
                          Cookies.set('Tel',this.tel);
                          
                           that.$message({
                            message: 'Ê≥®ÂÜåÊàêÂäü',
                            type: 'success'
                        });
                        that.$router.push("/");
                        console.log(resp.data);
                        })
                })
     }
   },
   data() {
    return {
      username: '',
      password: '',
      confirmPass: '',
      province:'',
      city:'',
      county:'',
      detail:'',
      tel:'',

    }
  }
})
</script>

