<template>
    <div>
        <el-input v-model="username" placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"></el-input>
        <el-input show-password v-model="password" placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"></el-input>

        <el-button type="primary" @click="login">ÁôªÂΩï</el-button>
        <h4>Ê≤°ÊúâË¥¶Âè∑?</h4>
        <el-button type="info" @click="register">Ê≥®ÂÜå</el-button>

    </div>
</template>
<script lang="ts">
import Vue from 'vue'
import axios from 'axios'
import Cookies from 'js-cookie'

const config = require('../config/conf.js');

export default Vue.extend({
   name: 'Login',
   created(){

   },
   methods:{
     login(){
       console.log(this.username);
       console.log(this.password);
      var that = this;
                axios({
                        method:'post',
                        url: config.url+ "login",
                        params:{
                            username: this.username,
                            password: this.password
                        }
                }).then(function(resp){
                        that.$nextTick(function () {
                       if(resp.data.Res){
                          Cookies.set('token',resp.data.Token);
                          Cookies.set('username',this.username);
                          Cookies.set('Province',resp.data.Info.province);
                          Cookies.set('City',resp.data.Info.city);
                          Cookies.set('County',resp.data.Info.county);
                          Cookies.set('Detail',resp.data.Info.detail);
                          Cookies.set('Tel',resp.data.Info.tel);
                            that.$message({
                              message: 'ÁôªÂΩïÊàêÂäü',
                              type: 'success'
                          });
                          that.$router.push("/");
                          }else{
                            that.$message({
                              message: 'Ë¥¶Âè∑ÊàñÂØÜÁ†ÅÈîôËØØ!',
                              type: 'fail'
                          });
                          }
                        console.log(resp.data);
                        })
                })
     },
     register(){
          console.log("Ê≥®ÂÜå");
          this.$router.push({
              path:'/Register',
          })
     }
   },
   data() {
    return {
      username: '',
      password: ''
    }
  }
})
</script>

